<template>
  <div>
    <SearchBox v-on:search="onSearch" search-title="Drug"/>
    <section v-if="loading" class="columns">
      <div class="box column is-half is-offset-one-quarter">
        <strong>Loading...</strong>
      </div>
    </section>
    <section v-if="loading == false">
      <ResultList :resultList="resultList"/>
    </section>
  </div>
</template>

<script>
  import SearchBox from '@/components/SearchBox'
  import ResultList from '@/components/ResultList'

  export default {
    name: 'DrugView',
    components: {
      SearchBox,
      ResultList,
    },
    data: function(){
      return {
        searchKeyword: '',
        resultList: {},
        loading: null,
        errored: false,
      }
    },
    methods: {
      fetchData: async function(){
        let url = 'https://us-central1-med-red-panda.cloudfunctions.net/fetch_data_medkit'
        // If in DEV mode
        // if(window.webpackHotUpdate) url = "http://localhost:8080"
        const urlParams = new URLSearchParams(window.location.search);

        let getParams = {
          keyword: this.searchKeyword,
          med: true,
        }
        if(urlParams.has('med')) getParams.med = true

        let res = await fetch(url+'?'+new URLSearchParams(getParams), {
          method: 'GET',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          },
        })
        if(res && res.ok){
          this.resultList = await res.json();
          this.loading = false
        }
        else {
          // console.log("HTTP-Error: " + res.status);
          this.errored = true
        }
      },
      onSearch: function(value){
        this.loading = true
        this.searchKeyword = value
        this.fetchData(value)
      },
    }
    
  }
</script>

